<template>
    <div
        id="date-picker-example"
        class="mt-0 mb-0 md-form md-outline input-with-post-icon datepicker"
    >
        <input
            type="text"
            class="form-control mb-0"
            :placeholder="placeholder"
            autocomplete="off"
            :value="value"
        />

        <i class="fa fa-calendar input-prefix" tabindex="0"></i>
    </div>
</template>

<script>
export default {
    name: "datepicker",
    props: ["value", "placeholder"],
    mounted: function() {
        const self = this;
        $(this.$el).datepicker({
            autoclose: true,
            format: "yyyy-mm-dd",
            onClose: function() {
                self.$emit("input", this.get("select", "yyyy-mm-dd"));
            }
        });
    },
    destroyed: function() {
        $(this.$el).datepicker("destroy");
    }
};
</script>
