<template>
    <div>
        <label>選項</label>
        <div class="form-group">
            <button
                data-toggle="tooltip"
                type="button"
                class="ml-0 btn btn-default btn-sm waves-effect waves-light"
                @click="openOptionModal"
            >選項管理</button>
        </div>
        <div class="modal fade" id="optionModal">
            <div class="modal-dialog modal-lg modal-dialog-centered">
                <div class="modal-content">
                    <!-- Modal Header -->
                    <div class="modal-header">
                        <h4 class="modal-title">選項管理</h4>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>
                    <!-- Modal body -->
                    <div class="modal-body">
                        <div class="form-group">
                            <div class="editor_options-wrap">
                                <div
                                    class="options-item"
                                    v-for="(option, index) in currentEdit.options"
                                    :key="`選項${index}`"
                                >
                                    <div class="options-hd">
                                        <span class="sort">
                                            <input
                                                v-model="option.value"
                                                class="form-control form-control-sm"
                                                @change="editOptionValue"
                                            />
                                        </span>
                                        <span class="additional">
                                            <div
                                                class="custom-control form-check form-inline pl-0"
                                                v-if="
                                    currentEdit.type == 'radio' ||
                                    currentEdit.type == 'checkbox'
                                "
                                            >
                                                <input
                                                    :id="`options_des_${index}`"
                                                    class="form-check-input"
                                                    type="checkbox"
                                                    v-model="option.des"
                                                    @change="checkInline"
                                                />
                                                <label
                                                    class="form-check-label"
                                                    :for="`options_des_${index}`"
                                                >額外輸入</label>
                                            </div>
                                        </span>
                                        <span class="action">
                                            <div class="btn-group">
                                                <button
                                                    title
                                                    data-toggle="tooltip"
                                                    type="button"
                                                    class="btn btn-xs btn-default waves-effect waves-light"
                                                    :disabled="index == 0"
                                                    @click="$upOption(currentEdit.options, index)"
                                                >
                                                    <i class="fa fa-chevron-up"></i>
                                                </button>
                                                <button
                                                    title
                                                    data-toggle="tooltip"
                                                    type="button"
                                                    class="btn btn-xs btn-default waves-effect waves-light"
                                                    :disabled="
                                            index == currentEdit.options.length - 1
                                        "
                                                    @click="$downOption(currentEdit.options, index)"
                                                >
                                                    <i class="fa fa-chevron-down"></i>
                                                </button>
                                                <button
                                                    title
                                                    data-toggle="tooltip"
                                                    type="button"
                                                    class="btn btn-xs btn-default waves-effect waves-light"
                                                    @click="$removeOption(currentEdit.options, index)"
                                                >
                                                    <i class="fa fa-times"></i>
                                                </button>
                                            </div>
                                        </span>
                                    </div>
                                    <div class="options-bd">
                                        <div
                                            class="language-item"
                                            v-for="lang in langs"
                                            :key="`選項內容${index}-${lang.key}`"
                                        >
                                            <label :for="`選項內容${index}-${lang.key}`">{{lang.label}}</label>
                                            <input
                                                :id="`選項內容${index}-${lang.key}`"
                                                v-model="option.label[lang.key]"
                                                class="form-control form-control-sm"
                                            />
                                        </div>

                                        <div
                                            class="additional-block"
                                            v-if="option.des == true && (currentEdit.type == 'radio' ||
                                    currentEdit.type == 'checkbox')"
                                        >
                                            <h6>額外輸入</h6>
                                            <div
                                                class="language-item"
                                                v-for="lang in langs"
                                                :key="`欄位提示${index}-${lang.key}`"
                                            >
                                                <label
                                                    :for="`欄位提示${index}-${lang.key}`"
                                                >{{lang.label}}</label>
                                                <input
                                                    :id="`欄位提示${index}-${lang.key}`"
                                                    v-model="option.placeholder[lang.key]"
                                                    class="form-control form-control-sm"
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <button
                                data-toggle="tooltip"
                                type="button"
                                class="ml-0 btn btn-default btn-sm waves-effect waves-light"
                                @click="addOption"
                            >新增項目</button>
                        </div>
                    </div>
                    <!-- Modal footer -->
                    <div class="modal-footer">
                        <button
                            type="button"
                            class="btn btn-sm waves-effect waves-light"
                            @click="$closeModal('option')"
                        >關閉</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "table_options",
    props: ["currentEdit", "langs"],
    mounted: function() {},
    methods: {
        addOption() {
            if (
                this.currentEdit.type == "radio" ||
                this.currentEdit.type == "checkbox"
            ) {
                this.currentEdit.options.push({
                    value: "",
                    label: {},
                    placeholder: { CH: "" }
                });
            } else {
                this.currentEdit.options.push({ value: "", label: {} });
            }
        },
        editOptionValue() {
            try {
                this.checkValue(this.currentEdit.options);
            } catch (error) {
                toastr["error"]("Error", error.message);
                return;
            }
        },
        checkValue(arr) {
            // 檢查 options value 是否重複
            if (arr && arr.length > 0) {
                if (
                    new Set(arr.map(item => item.value.toString())).size <
                    arr.length
                ) {
                    // 重複
                    throw new Error("索引不可重複");
                }
            }
        },
        checkInline() {
            let des = this.currentEdit.options.some(item => {
                if (item.des) {
                    this.currentEdit.setting.inline = false;
                    return true;
                }
            });
            if (!des && this.currentEdit.setting.inline) {
                this.currentEdit.setting.inline = true;
            }
        },
        openOptionModal() {
            $(`#optionModal`)
                .appendTo("body")
                .modal("show");
        }
    }
};
</script>
