<template>
    <div id="form_editor">
        <button
            type="button"
            class="btn btn-primary btn-sm waves-effect waves-light form_editor_save"
            @click="saveEditor"
        >儲存</button>
        <div class="row form_editor_row">
            <div class="side_element">
                <ul class="nav nav-tabs mb-0 form_edite_nav">
                    <li class="nav-item" @click="changeContnet('insert')">
                        <a class="nav-link" :class="{ active: contentType == 'insert' }">新增欄位</a>
                    </li>
                    <li
                        class="nav-item"
                        @click="changeContnet('edit')"
                        v-if="defaultEditContents.length > 0"
                    >
                        <a class="nav-link" :class="{ active: contentType == 'edit' }">現有欄位</a>
                    </li>
                </ul>
                <div class="col-block side_element_block">
                    <!-- 新增欄位 -->
                    <div class="block-content" v-if="contentType == 'insert'">
                        <div class="mt-0 md-form md-outline input-with-post-icon search">
                            <input
                                type="search"
                                class="form-control rounded"
                                placeholder="搜尋元件"
                                aria-label="Search"
                                aria-describedby="search-addon"
                                v-model="searchWords"
                            />

                            <i class="fa fa-search input-prefix"></i>
                        </div>
                        <select
                            class="md-form md-outline browser-default custom-select"
                            v-model="insertType"
                        >
                            <option
                                v-for="(type, insertTypeIdx) in insertTypeList"
                                :key="'type_' + insertTypeIdx"
                                :value="type.value"
                            >{{ type.label }}</option>
                            <option v-if="cbData.dev" value="defaultItem">固定欄位</option>
                        </select>
                        <small class="form-text text-muted mb-2 side_element_des">
                            <i class="fa fa-lock"></i> 項目不可重複選取
                            <br />
                            <i class="icon-repect"></i> 項目僅能使用一次
                        </small>
                        <!-- 功能區塊 -->
                        <draggable
                            class="dragArea list-group insert-area"
                            :list="filterInsertContents"
                            :group="{ name: 'editor', pull: 'clone', put: false }"
                            :clone="onClone"
                            :sort="false"
                            @change="log"
                            @start="onDragStart"
                            @end="onDragEnd"
                            draggable=".addable"
                        >
                            <div
                                class="list-group-item"
                                v-for="(content, filterInsertIdx) in filterInsertContents"
                                :key="'filterInsert_' + filterInsertIdx"
                                :class="{'addable': content.addable != false,'disabled': content.addable == false}"
                            >
                                <div class="list-group-inner">
                                    <div class="group-hd">
                                        <i
                                            class="fa"
                                            :class="{'fa-cog': content.addable != false && content.insertType == 'default', 'icon-repect': content.addable != false && content.insertType != 'default' ,'fa-lock': content.addable == false}"
                                        ></i>
                                        <span>{{ content.label.CH }}</span>
                                    </div>
                                    <span
                                        class="tooltip-area"
                                        data-toggle="tooltip"
                                        :title="content.insertDes"
                                        v-if="content.insertDes"
                                    ></span>
                                </div>
                            </div>
                        </draggable>
                    </div>
                    <!-- 新增欄位 end -->
                    <!-- 現有欄位 -->
                    <div class="block-content" v-else>
                        <div class="mt-0 md-form md-outline input-with-post-icon search">
                            <input
                                type="search"
                                class="form-control rounded"
                                placeholder="搜尋元件"
                                aria-label="Search"
                                aria-describedby="search-addon"
                                v-model="searchWords"
                            />

                            <i class="fa fa-search input-prefix"></i>
                        </div>

                        <!-- 功能區塊 -->
                        <draggable
                            class="dragArea list-group insert-area"
                            :list="filterEditContents"
                            :group="{ name: 'editor', pull: 'clone', put: false }"
                            :clone="onClone"
                            :sort="false"
                            @change="log"
                            @start="onDragStart"
                            @end="onDragEnd"
                            draggable=".addable"
                        >
                            <div
                                class="list-group-item"
                                v-for="(content, filterEditIdx) in filterEditContents"
                                :key="'filterEdit_' + filterEditIdx"
                                :class="{'addable': content.addable != false,'disabled': content.addable == false}"
                            >
                                <div class="list-group-inner">
                                    <div class="group-hd">
                                        <i class="fa fa-cog"></i>
                                        <span>{{ content.label.CH }}</span>
                                    </div>
                                    <span
                                        class="tooltip-area"
                                        data-toggle="tooltip"
                                        :title="content.insertDes"
                                        v-if="content.insertDes"
                                    ></span>
                                </div>
                            </div>
                        </draggable>
                    </div>
                    <!-- 現有欄位 end -->
                </div>
            </div>
            <div class="edit_area">
                <!-- tabs -->
                <ul class="nav nav-tabs mb-0 form_edite_nav">
                    <li
                        class="nav-item"
                        @click="changeI18n(lang.key)"
                        v-for="lang in enumData.langs"
                        :key="'nav_' + lang.key"
                    >
                        <a class="nav-link" :class="{ active: i18n == lang.key }">{{lang.label}}</a>
                    </li>
                </ul>
                <!-- tabs -->

                <draggable v-model="defaultContents" @change="log" class="edit-container">
                    <div
                        class="col-block edit_area_block"
                        v-for="(contents, divIndex) in defaultContents"
                        :key="'div_' + divIndex"
                    >
                        <div class="block-header has-border" @dblclick="editContent(contents)">
                            <h3>
                                <strong>{{ contents.label[i18n] }}</strong>
                                <small
                                    class="form-text text-muted"
                                    v-if="contents.des && contents.des[i18n]"
                                >{{ contents.des[i18n] }}</small>
                            </h3>
                            <button
                                type="button"
                                class="close"
                                aria-label="Close"
                                v-if="checkDivRemoveAble(contents.items)"
                                @click="removeAt(divIndex)"
                            >
                                <span aria-hidden="true">×</span>
                            </button>
                            <button
                                type="button"
                                class="close btn-light"
                                aria-label="Close"
                                title="區塊內含有無法刪除之元件"
                                v-else
                            >
                                <span aria-hidden="true">×</span>
                            </button>
                        </div>
                        <div class="block-content">
                            <form>
                                <!-- 編輯區塊 -->
                                <draggable
                                    class="dragArea list-group"
                                    :list="contents.items"
                                    group="editor"
                                    @change="log"
                                    @add="onAdd(divIndex,$event)"
                                >
                                    <div
                                        class="form-group dragDiv"
                                        v-for="(content, itemIndex) in contents.items"
                                        :key="'item_' + itemIndex"
                                        :class="{ 'edit-active': content.editorEnable }"
                                        @dblclick="editContent(content, divIndex, itemIndex)"
                                    >
                                        <button
                                            type="button"
                                            class="close"
                                            aria-label="Close"
                                            @click="removeAt(divIndex, itemIndex)"
                                            v-if="content.editorEnable && content.setting.removeable != false"
                                        >
                                            <span aria-hidden="true">×</span>
                                        </button>
                                        <span
                                            class="form-request"
                                            v-if="content.setting && content.setting.require"
                                        >*</span>
                                        <label
                                            :for="`content${divIndex}_${itemIndex}`"
                                            v-if="content.type != 'des' && content.setting.showTitle != false"
                                        >{{ content.label[i18n] }}</label>
                                        <small
                                            class="form-text text-muted"
                                            v-if="
                                              content.des &&
                                              content.des[i18n] &&
                                              content.setting.showDes != false && 
                                              content.setting.showDesOnTop == true
                                            "
                                        >{{ content.des[i18n] }}</small>
                                        <!-- des 說明文字 -->
                                        <div
                                            v-if="content.type == 'des'"
                                            v-html="content.content[i18n]"
                                        ></div>
                                        <!-- des end -->
                                        <!-- text 單行文字 -->
                                        <input
                                            v-else-if="content.type == 'text'"
                                            class="form-control"
                                            :class="content.setting.width || 'col-md-12'"
                                            :id="`content${divIndex}_${itemIndex}`"
                                            :minlength="content.setting.min"
                                            :maxlength="content.setting.max"
                                            :placeholder="
                                              content.setting.placeholder &&
                                              content.setting.placeholder[i18n]
                                            "
                                            :value="content.value && content.value[i18n]"
                                        />
                                        <!-- text end -->
                                        <!-- textarea 多行文字 -->
                                        <textarea
                                            v-else-if="content.type == 'textarea'"
                                            class="form-control"
                                            :class="content.setting.width || 'col-md-12'"
                                            :id="`content${divIndex}_${itemIndex}`"
                                            :minlength="content.setting.min"
                                            :maxlength="content.setting.max"
                                            :rows="content.setting.rows"
                                            :placeholder="
                                              content.setting.placeholder &&
                                              content.setting.placeholder[i18n]
                                            "
                                            :value="content.value && content.value[i18n]"
                                        ></textarea>
                                        <!-- textarea end -->
                                        <!-- email 電子郵件 -->
                                        <input
                                            v-if="content.type == 'email'"
                                            class="form-control"
                                            type="email"
                                            :class="content.setting.width || 'col-md-12'"
                                            :id="`content${divIndex}_${itemIndex}`"
                                            :placeholder="
                                              content.setting.placeholder &&
                                              content.setting.placeholder[i18n]
                                            "
                                            :value="content.value && content.value[i18n]"
                                        />
                                        <!-- email end -->
                                        <!-- number 數字 -->
                                        <input
                                            v-if="content.type == 'number'"
                                            class="form-control"
                                            type="number"
                                            :class="content.setting.width || 'col-md-12'"
                                            :id="`content${divIndex}_${itemIndex}`"
                                            :min="content.setting.min"
                                            :max="content.setting.max"
                                            :step="content.setting.step"
                                            :placeholder="
                                              content.setting.placeholder &&
                                              content.setting.placeholder[i18n]
                                            "
                                            :value="content.value"
                                        />
                                        <!-- number end -->
                                        <!-- datepicker -->
                                        <div v-else-if="content.type == 'datepicker'" class="row">
                                            <div :class="content.setting.width || 'col-md-12'">
                                                <datepicker
                                                    :value="content.date"
                                                    :placeholder="
                                                      content.setting.placeholder &&
                                                      content.setting.placeholder[i18n]
                                                    "
                                                ></datepicker>
                                            </div>
                                        </div>
                                        <!-- datepicker end -->

                                        <!-- datetimepicker -->
                                        <div
                                            v-else-if="content.type == 'datetimepicker'"
                                            class="row"
                                        >
                                            <div :class="content.setting.width || 'col-md-12'">
                                                <datetimepicker
                                                    :value="content.datetime"
                                                    :placeholder="
                                                      content.setting.placeholder &&
                                                      content.setting.placeholder[i18n]
                                                    "
                                                ></datetimepicker>
                                            </div>
                                        </div>
                                        <!-- datetimepicker end -->
                                        <!-- select 下拉選項 -->
                                        <select
                                            v-else-if="content.type == 'select'"
                                            class="browser-default custom-select"
                                            :class="content.setting.width || 'col-md-12'"
                                            :id="`content${divIndex}_${itemIndex}`"
                                        >
                                            <option
                                                v-for="(option, optionIdx) in content.options"
                                                :key="'下拉選項_' + optionIdx"
                                                :value="option.value"
                                            >{{ option.label[i18n] }}</option>
                                        </select>
                                        <!-- select end -->
                                        <!-- radio 單選項目 -->
                                        <div
                                            v-else-if="content.type == 'radio'"
                                            :class="content.setting.width || 'col-md-12'"
                                        >
                                            <div
                                                v-for="(option, optionIdx) in content.options"
                                                class="form-check"
                                                :key="'單選項目_' + optionIdx"
                                                :class="{
                                                  'form-inline': !content.setting.inline,
                                                  'custom-control-inline': content.setting.inline,
                                                  'mb-2': option.des,
                                                }"
                                            >
                                                <input
                                                    type="radio"
                                                    :id="`option_radio_${itemIndex}_${optionIdx}`"
                                                    :name="`radio_${itemIndex}`"
                                                    :value="option.value"
                                                    class="form-check-input"
                                                />
                                                <label
                                                    class="form-check-label"
                                                    :for="`option_radio_${itemIndex}_${optionIdx}`"
                                                >{{ option.label[i18n] }}</label>
                                                <input
                                                    type="text"
                                                    class="form-control form-control-sm ml-2"
                                                    v-if="option.des"
                                                    :placeholder="option.placeholder[i18n]"
                                                />
                                            </div>
                                        </div>
                                        <!-- radio end -->
                                        <!-- checkbox 複選項目 -->
                                        <div
                                            v-else-if="content.type == 'checkbox'"
                                            :class="content.setting.width || 'col-md-12'"
                                        >
                                            <div
                                                v-for="(option, optionIdx) in content.options"
                                                class="form-check"
                                                :key="'複選項目_' + optionIdx"
                                                :class="{
                                                  'form-inline': !content.setting.inline,
                                                  'custom-control-inline': content.setting.inline,
                                                  'mb-2': option.des,
                                                }"
                                            >
                                                <input
                                                    type="checkbox"
                                                    :id="`option_checkbox_${itemIndex}_${optionIdx}`"
                                                    :name="`checkbox_${itemIndex}`"
                                                    :value="option.value"
                                                    class="form-check-input"
                                                />
                                                <label
                                                    class="form-check-label"
                                                    :for="`option_checkbox_${itemIndex}_${optionIdx}`"
                                                >{{ option.label[i18n] }}</label>
                                                <input
                                                    type="text"
                                                    class="form-control form-control-sm ml-2"
                                                    v-if="option.des"
                                                    :placeholder="option.placeholder[i18n]"
                                                />
                                            </div>
                                        </div>
                                        <!-- checkbox end -->

                                        <!-- file upload 檔案上傳 -->
                                        <div v-else-if="content.type == 'file_upload'">
                                            <input
                                                class="form-control-file disable-click"
                                                :accept="`.${content.setting.accept.join(',.')}`"
                                                type="file"
                                                style="display: none;"
                                            />
                                            <button
                                                type="button"
                                                class="ml-0 btn btn-xs btn-success waves-effect waves-light"
                                                v-if="content.file"
                                                @click="downloadFile(content.file)"
                                            >範本</button>
                                            <button
                                                type="button"
                                                class="ml-0 btn btn-xs btn-primary waves-effect waves-light"
                                            >選擇檔案</button>
                                        </div>

                                        <!-- file upload end -->
                                        <!-- file download 檔案下載 -->
                                        <div v-else-if="content.type == 'file_download'">
                                            <button
                                                type="button"
                                                class="ml-0 btn btn-xs btn-primary waves-effect waves-light"
                                                @click="downloadFile(content.file)"
                                            >檔案下載</button>
                                        </div>
                                        <!-- file download end -->

                                        <!-- custom -->
                                        <customComponents
                                            type="edit"
                                            :i18n="i18n"
                                            :content="content"
                                            :enumData="enumData"
                                            ref="customComponents"
                                        ></customComponents>
                                        <!-- custom end -->
                                        <small
                                            class="form-text text-muted"
                                            v-if="
                                              content.des &&
                                              content.des[i18n] &&
                                              content.setting.showDes != false && 
                                              content.setting.showDesOnTop != true
                                            "
                                        >{{ content.des[i18n] }}</small>
                                    </div>
                                </draggable>
                            </form>
                        </div>
                    </div>
                </draggable>
                <button
                    type="button"
                    class="btn btn-light-blue btn-lg btn-block waves-effect waves-light mt-20"
                    @click="addDIV"
                >建立 DIV 區塊</button>
            </div>
            <div class="side_field">
                <div class="col-block side_field_bloxk">
                    <div class="block-header has-border">
                        <h3>
                            <strong>欄位設定</strong>
                        </h3>
                    </div>
                    <div class="block-content edit-area">
                        <form v-if="Object.keys(currentEdit).length != 0">
                            <template v-if="cbData.fieldName && !editDiv">
                                <div class="form-group">
                                    <label for="fieldName">欄位名稱</label>
                                    <input
                                        v-model="currentEdit.fieldName"
                                        id="fieldName"
                                        class="form-control form-control-sm"
                                    />
                                </div>
                            </template>
                            <!-- 除說明文字外的元件 -->
                            <div v-if="currentEdit.type != 'des'">
                                <template
                                    v-if="currentEdit.setting && currentEdit.setting.showTitle != false || editDiv"
                                >
                                    <div
                                        class="form-group"
                                        v-for="(lang, langidx) in enumData.langs"
                                        :key="'標題_' + lang.key"
                                    >
                                        <label :for="`標題${langidx}`">標題{{ lang.label }}</label>
                                        <input
                                            v-model="currentEdit.label[lang.key]"
                                            :id="`標題${langidx}`"
                                            class="form-control form-control-sm"
                                        />
                                    </div>
                                </template>
                                <template
                                    v-if="currentEdit.setting && currentEdit.setting.showDes != false || editDiv"
                                >
                                    <div
                                        class="form-group"
                                        v-for="(lang, langidx) in enumData.langs"
                                        :key="'說明_' + lang.key"
                                    >
                                        <label :for="`說明${langidx}`">說明{{ lang.label }}</label>
                                        <textarea
                                            v-model="currentEdit.des[lang.key]"
                                            :id="`說明${lang.key}`"
                                            class="form-control form-control-sm"
                                            rows="3"
                                        ></textarea>
                                    </div>
                                </template>
                                <template v-if="!editDiv">
                                    <div class="form-group" v-if="currentEdit.type == 'textarea'">
                                        <label for="textarea_rows">行數</label>
                                        <input
                                            v-model="currentEdit.setting.rows"
                                            id="textarea_rows"
                                            class="form-control form-control-sm"
                                        />
                                    </div>
                                    <template
                                        v-if="
                                        currentEdit.type == 'text' ||
                                        currentEdit.type == 'textarea' ||
                                        currentEdit.type == 'email' ||
                                        currentEdit.type == 'number' ||
                                        currentEdit.type == 'datepicker' ||
                                        currentEdit.type == 'datetimepicker'
                                        "
                                    >
                                        <!-- 預設日期 -->
                                        <div
                                            class="form-group"
                                            v-if="
                                            currentEdit.type == 'datepicker'
                                            "
                                        >
                                            <label for="placeholer">預設資料</label>
                                            <datepicker v-model="currentEdit.date"></datepicker>
                                        </div>
                                        <!-- 預設日期時間 -->
                                        <div
                                            class="form-group"
                                            v-else-if="currentEdit.type == 'datetimepicker'"
                                        >
                                            <label for="placeholer">預設資料</label>
                                            <datetimepicker v-model="currentEdit.datetime"></datetimepicker>
                                        </div>
                                        <!-- 預設數字 -->
                                        <div
                                            class="form-group"
                                            v-else-if="currentEdit.type == 'number'"
                                        >
                                            <label for="placeholer">預設資料</label>
                                            <input
                                                v-model="currentEdit.value"
                                                id="placeholer"
                                                class="form-control form-control-sm"
                                            />
                                        </div>
                                        <!-- 其他預設 -->
                                        <div v-else>
                                            <div
                                                class="form-group"
                                                v-for="(lang, langidx) in enumData.langs"
                                                :key="'預設資料_' + lang.key"
                                            >
                                                <label :for="`預設資料${langidx}`">預設資料{{ lang.label }}</label>
                                                <input
                                                    v-model="currentEdit.value[lang.key]"
                                                    :id="`預設資料${langidx}`"
                                                    class="form-control form-control-sm"
                                                />
                                            </div>
                                        </div>

                                        <div
                                            class="form-group"
                                            v-for="(lang, langidx) in enumData.langs"
                                            :key="'提示文字_' + lang.key"
                                        >
                                            <label :for="`提示文字${langidx}`">提示文字{{ lang.label }}</label>
                                            <input
                                                v-model="currentEdit.setting.placeholder[lang.key]"
                                                :id="`提示文字${langidx}`"
                                                class="form-control form-control-sm"
                                            />
                                        </div>
                                    </template>

                                    <div
                                        class="form-group"
                                        v-if="currentEdit.setting && currentEdit.setting.showWidth != false"
                                    >
                                        <!-- 檔案上傳下載 地址 表格 不可選欄位寬度 -->
                                        <label>欄位，寬度佔表單寬度的比例</label>
                                        <div
                                            class="custom-control form-check custom-control-inline"
                                        >
                                            <input
                                                v-model="currentEdit.setting.width"
                                                value="col-md-12"
                                                type="radio"
                                                id="width100"
                                                name="formwidth"
                                                class="form-check-input"
                                            />
                                            <label class="form-check-label" for="width100">100%</label>
                                        </div>
                                        <div
                                            class="custom-control form-check custom-control-inline"
                                        >
                                            <input
                                                v-model="currentEdit.setting.width"
                                                value="col-md-8"
                                                type="radio"
                                                id="width75"
                                                name="formwidth"
                                                class="form-check-input"
                                            />
                                            <label class="form-check-label" for="width75">75%</label>
                                        </div>
                                        <div
                                            class="custom-control form-check custom-control-inline"
                                        >
                                            <input
                                                v-model="currentEdit.setting.width"
                                                value="col-md-6"
                                                type="radio"
                                                id="width50"
                                                name="formwidth"
                                                class="form-check-input"
                                            />
                                            <label class="form-check-label" for="width50">50%</label>
                                        </div>
                                        <div
                                            class="custom-control form-check custom-control-inline"
                                        >
                                            <input
                                                v-model="currentEdit.setting.width"
                                                value="col-md-3"
                                                type="radio"
                                                id="width25"
                                                name="formwidth"
                                                class="form-check-input"
                                            />
                                            <label class="form-check-label" for="width25">25%</label>
                                        </div>
                                    </div>
                                    <div
                                        class="form-group"
                                        v-if="currentEdit.setting.showSetting != false"
                                    >
                                        <label>欄位選項</label>
                                        <div>
                                            <div
                                                class="form-check"
                                                v-for="setting in filterSettings"
                                                :key="'欄位選項_' + setting.key"
                                            >
                                                <input
                                                    class="form-check-input"
                                                    type="checkbox"
                                                    v-model="currentEdit.setting[setting.key]"
                                                    :id="`setting_${setting.key}`"
                                                />
                                                <label
                                                    class="form-check-label"
                                                    :for="`setting_${setting.key}`"
                                                >{{ setting.label }}</label>
                                            </div>
                                        </div>
                                    </div>
                                    <!-- 選項排列方式 -->
                                    <template
                                        v-if="
                                            currentEdit.type == 'radio' ||
                                            currentEdit.type == 'checkbox'
                                        "
                                    >
                                        <displayInline
                                            :currentEdit="currentEdit"
                                            :langs="enumData.langs"
                                        ></displayInline>
                                    </template>

                                    <!-- 下拉選項 -->
                                    <template
                                        v-if="
                                            currentEdit.type == 'select' ||
                                            currentEdit.type == 'radio' ||
