                timeOut: "10000",
                extendedTimeOut: "3000",
                showEasing: "swing",
                hideEasing: "linear",
                showMethod: "fadeIn",
                hideMethod: "fadeOut"
            }
        };
    },
    computed: {
        allContents() {
            // 所有 item 移除階層
            let arr = [];
            this.defaultContents.forEach(item => {
                arr = arr.concat(item.items);
            });
            return arr;
        },
        defaultItems() {
            return this.defaultInsertContents.filter(insertItem => {
                return insertItem.insertType == "defaultItem";
            });
        },
        hideDefalutItemInsertContents() {
            // mode dev 才顯示 defaultItems
            return this.defaultInsertContents.filter(insertItem => {
                return this.cbData.dev
                    ? true
                    : insertItem.insertType != "defaultItem";
            });
        },
        checkAddInsertContents() {
            // 是否可新增 新增欄位
            // 除了 基本元件外 其餘頁面中只能有一個 check type && insertType
            return this.hideDefalutItemInsertContents.map(insertItem => {
                insertItem.addable = true;
                this.allContents.forEach(contentItem => {
                    if (
                        contentItem.type == insertItem.type &&
                        insertItem.insertType != "default"
                    ) {
                        insertItem.addable = false;
                    }
                });
                return insertItem;
            });
        },
        filterInsertContents() {
            return this.checkAddInsertContents
                .filter(searchResult =>
                    searchResult.label.CH.match(this.searchWords)
                )
                .filter(typeResult => {
                    if (this.insertType) {
                        return typeResult.insertType == this.insertType;
                    } else {
                        return true;
                    }
                });
        },
        checkAddEditContents() {
            // 是否可新增 現有欄位
            // 頁面中只能有一個 check uuid
            let arr = [];

            this.defaultEditContents.forEach(item => {
                arr = arr.concat(item.items);
            });

            return arr.map(insertItem => {
                insertItem.addable = true;
                this.allContents.forEach(contentItem => {
                    if (contentItem.uuid == insertItem.uuid) {
                        insertItem.addable = false;
                    }
                });
                return insertItem;
            });
        },
        filterEditContents() {
            return this.checkAddEditContents
                .filter(searchResult =>
                    searchResult.label.CH.match(this.searchWords)
                )
                .filter(typeResult => {
                    if (this.insertType) {
                        return typeResult.insertType == this.insertType;
                    } else {
                        return true;
                    }
                });
        },
        rulesTypeArray() {
            // 規則的類型 array
            return this.enumData.rules.map(item => {
                return item.type;
            });
        },
        rulesInType() {
            // 在此類型下的 rules
            return this.enumData.rules.filter(item => {
                return item.type == this.currentEdit.type || item.type == "";
            });
        },
        filterSettings() {
            // 檔案上傳 只顯示 必填欄位
            return this.enumData.settings.filter(setting => {
                return (
                    this.currentEdit.type != "file_upload" ||
                    setting.key == "require" ||
                    setting.key == "invisiable"
                );
            });
        }
    },
    mounted() {
        toastr.options = this.toastrOptions;

        const el = document.querySelector("#cbdata");
        this.cbData = JSON.parse(el.dataset.cbdata);

        // 載入擴充元件
        this.defaultInsertContents = this.defaultInsertContents.concat(
            this.$refs.customComponents[0].defaultInsertContents
        );
        // 取得欄位
        this.getContent();
        // 取得現有欄位
        if (this.cbData.exampleId) {
            this.getEditContent();
        }
    },
    methods: {
        removeAt(divIndex, itemIndex) {
            const thenCB = () => {
                if (itemIndex != undefined) {
                    // 移除 itme
                    this.defaultContents[divIndex].items.splice(itemIndex, 1);
                } else {
                    // 移除 div
                    this.defaultContents.splice(divIndex, 1);
                }
                this.currentEdit = {};
            };
            this.$confirm("提示", "是否確定刪除?", thenCB);
        },
        log(evt) {
            console.log("log", evt);
        },
        onDragStart(e) {
            e.target.classList.add("grabbing");
        },
        onDragEnd(e) {
            e.target.classList.remove("grabbing");
        },
        onAdd(divIndex, evt) {
            this.closeEdit();
            // 新增item時 增加uuid
            let content = this.defaultContents[divIndex].items[evt.newIndex];
            if (!content.uuid) {
                this.$set(content, "uuid", this.$uuid());
            }
            // focus 新增區塊
            this.editContent(content, divIndex, evt.newIndex);
        },
        onClone(evt) {
            // 改寫 clone 為 deep copy 修改值才不會變更原本的
            return JSON.parse(JSON.stringify(evt));
        },
        editContent(content, divIndex, itemIndex) {
            try {
                this.checkValue(this.currentEdit.options);
            } catch (error) {
                toastr["error"]("Error", error.message);
                return;
            }

            this.closeEdit();
            if (divIndex != undefined) {
                if (
                    this.defaultContents[divIndex].items[itemIndex] != undefined
                ) {
                    this.editDiv = false;
                    this.$set(
                        this.defaultContents[divIndex].items[itemIndex],
                        "editorEnable",
                        true
                    );
                    this.currentDivIndex = divIndex;
                    this.currentItemIndex = itemIndex;
                    this.currentEdit = content;
                }
            } else {
                this.editDiv = true;
                this.currentEdit = content;
            }
            // this.currentEdit = Object.assign({}, content);
        },
        closeEdit() {
            this.defaultContents.forEach((div, divIndex) => {
                this.defaultContents[divIndex].items.forEach(
                    (item, itemIndex) => {
                        this.$set(
                            this.defaultContents[divIndex].items[itemIndex],
                            "editorEnable",
                            false
                        );
                    }
                );
            });
        },
        addDIV() {
            this.defaultContents.push({
                label: { CH: "名稱", EN: "DIV" },
                des: {},
                items: []
            });
        },
        changeI18n(lang) {
            this.i18n = lang;
        },
        changeContnet(type) {
            this.contentType = type;
        },
        uploadFile(event) {
            // 上傳檔案 取得url 放入 item
            let formData = new FormData();
            formData.append("uid", this.cbData.uid);
            formData.append("file", event.target.files[0]);

            fetch(this.cbData.uploadApi, {
                method: "POST",
                body: formData
            })
                .then(res => res.json())
                .catch(err => {
                    toastr["error"]("Error", err);
                })
                .then(res => {
                    if (res.success) {
                        this.currentEdit.file = res.url;
                    } else {
                        toastr["error"]("Error", res.message);
                    }
                });
        },
        downloadFile(url) {
            // var filename = url
            //     .split("\\")
            //     .pop()
            //     .split("/")
            //     .pop();
            // var a = document.createElement("a");
            // a.href = url;
            // a.setAttribute("download", filename);
            // a.click();
            window.open(url);
        },
        checkInline() {
            let des = this.currentEdit.options.some(item => {
                if (item.des) {
                    this.currentEdit.setting.inline = false;
                    return true;
                }
            });
            if (!des && this.currentEdit.setting.inline) {
                this.currentEdit.setting.inline = true;
            }
        },
        saveEditor() {
            try {
                this.defaultContents.forEach(div => {
                    div.items.forEach(item => {
                        this.checkValue(item.options);
                    });
                });
            } catch (error) {
                toastr["error"]("Error", error.message);
                return;
            }

            let formData = new FormData();

            formData.append("uid", this.cbData.uid);
            formData.append("formId", this.cbData.formId);
            formData.append("data", JSON.stringify(this.defaultContents));

            fetch(this.cbData.api, {
                method: "POST",
                body: formData
            })
                .then(res => res.json())
                .catch(err => {
                    toastr["error"]("Error", err);
                })
                .then(res => {
                    if (res.success) {
                        toastr["success"]("Success", "儲存成功");
                    } else {
                        toastr["error"]("Error", res.message);
                    }
                });
        },
        getContent() {
            fetch(
                this.cbData.api +
                    `uid/${this.cbData.uid}/formId/${this.cbData.formId}`
            )
                .then(res => res.json())
                .catch(err => {
                    toastr["error"]("Error", err);
                })
                .then(data => {
                    this.enumData = data.enumData;
                    this.unshiftDefaultEvt(data.data);
                });
        },
        getEditContent() {
            fetch(
                this.cbData.api +
                    `uid/${this.cbData.uid}/formId/${this.cbData.exampleId}`
            )
                .then(res => res.json())
                .catch(err => {
                    toastr["error"]("Error", err);
                })
                .then(data => {
                    this.enumData = data.enumData;
                    this.defaultEditContents = data.data;
                });
        },
        unshiftDefaultEvt(data) {
            let defaultType = this.cbData.default; // 預設元件種類，非陣列 (例如：false) 則不做預設元件的處理
            let customEvt; // 預設元件
            let clearEvt; // 將清除的元件
            let clearType = []; // 將清除的元件種類

            if (Array.isArray(defaultType)) {
                // 取得 defaultType 以外的固定元件
                clearEvt = this.defaultItems.filter(item => {
                    return !defaultType.includes(item.type);
                });
                clearEvt.forEach(clearItem => {
                    clearType.push(clearItem.type);
                });

                // 清除 defaultType 以外的固定元件
                data.forEach((contents, index) => {
                    data[index]["items"] = contents.items.filter(item => {
                        return !clearType.includes(item.type);
                    });
                });

                // 若元件已存在則從 defaultType 內移除
                data.forEach(contents => {
                    defaultType = defaultType.filter(type => {
                        let re = true;
                        contents.items.forEach(item => {
                            if (item.type == type) {
                                re = false;
                            }
                        });
                        return re;
                    });
                });
            }

            // 產生 DIV 並放入元件
            if (Array.isArray(defaultType) && defaultType.length > 0) {
                customEvt = this.defaultItems.filter(typeResult => {
                    return defaultType.includes(typeResult.type);
                });

                data.unshift({
                    label: { CH: "系統產生", EN: "" },
                    des: {},
                    items: customEvt
                });
            }

            this.defaultContents = data;

            // 產生 uuid，觸發編輯以觸發元件的 created
            if (Array.isArray(defaultType) && defaultType.length > 0) {
                this.closeEdit();
                customEvt.forEach((evt, evtIndex) => {
                    if (!evt.uuid) {
                        this.$set(evt, "uuid", this.$uuid());
                    }

                    this.editContent(evt, 0, evtIndex);
                });
            }
        },
        checkValue(arr) {
            // 檢查 options value 是否重複
            if (arr && arr.length > 0) {
                if (
                    new Set(arr.map(item => item.value.toString())).size <
                    arr.length
                ) {
                    // 重複
                    throw new Error("索引不可重複");
                }
            }
        },
        checkDivRemoveAble(items) {
            return items.every(item => {
                return (
                    typeof item.setting == "undefined" ||
                    item.setting.removeable != false
                );
            });
        }
    }
};
</script>
