<script>
import datepicker from "@/components/Datepicker";

export default {
    name: "semester",
    components: {
        datepicker
    },
    props: {
        type: {
            type: String,
            required: true,
            default: ""
        },
        i18n: {
            type: String,
            required: true,
            default: ""
        },
        content: {
            type: Object,
            default: function() {
                return {};
            }
        },
        currentEdit: {
            type: Object,
            default: function() {
                return {};
            }
        },
        enumData: {
            type: Object,
            default: function() {
                return {};
            }
        }
    },
    data() {
        return {
            // 學期志願選項
            semesterEdit: {
                label: {},
                startDate: "",
                endDate: ""
            },
            semesterInit: {
                label: {},
                startDate: "",
                endDate: ""
            },
            semesterEditIndex: "",
            schoolEdit: {
                name: {},
                cityId: ""
            },
            schoolInit: {
                name: {},
                cityId: ""
            },
            schoolEditIndex: ""
        };
    },
    computed: {
        cityList: function() {
            if (this.enumData.cities) {
                return Object.entries(this.enumData.cities).map(
                    ([key, value]) => {
                        return {
                            id: key,
                            name: value
                        };
                    }
                );
            } else {
                return [];
            }
        }
    },
    created() {},
    methods: {
        addOption(name, initObj) {
            this.currentEdit[name].push(initObj);
        },
        openOptionModal(name) {
            $(`#option${name}Modal`)
                .appendTo("body")
                .modal("show");
        }
    }
};
</script>
