<template>
    <div v-if="type == 'edit'">
        <!-- 編輯區塊 -->
        <!-- wish 學期志願選項 -->
        <div class="form-group">
            <div
                class="form-inline mb-2"
                v-for="(wish, wishIndex) in content.wish"
                :key="'wish_' + wishIndex"
            >
                <select
                    class="browser-default custom-select input-group"
                    v-model="selectedRankList[wishIndex]"
                >
                    <option value></option>
                    <option
                        v-for="num in parseInt(content.setting.limit)"
                        :key="'limit_no' + num"
                        :value="num"
                        :disabled="rankIsSeleted(num)"
                    >{{ num }}</option>
                </select>
                <label class="ml-2">{{ getAreaText(wish.areaId) }} {{ wish.name[i18n] }}</label>
            </div>
        </div>
    </div>
    <div v-else>
        <!-- 欄位設定 -->
        <!-- wish 志願選項 -->
        <div class="form-group">
            <label for="wish_limit">開放填寫志願數</label>
            <input
                class="form-control form-control-sm"
                type="number"
                v-model="currentEdit.setting.limit"
                id="wish_limit"
                min="0"
            />
        </div>
        <label>志願地區</label>
        <div class="form-group">
            <button
                data-toggle="tooltip"
                type="button"
                class="ml-0 btn btn-default btn-sm waves-effect waves-light"
                @click="openOptionModal"
            >選項管理</button>
        </div>
        <div class="modal fade" id="optionModal">
            <div class="modal-dialog modal-lg modal-dialog-centered">
                <div class="modal-content">
                    <!-- Modal Header -->
                    <div class="modal-header">
                        <h4 class="modal-title">選項管理</h4>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>
                    <!-- Modal body -->
                    <div class="modal-body">
                        <div class="form-group">
                            <div class="editor_options-wrap">
                                <div
                                    class="options-item"
                                    v-for="(data, index) in currentEdit.wish"
                                    :key="'wish_' + index"
                                >
                                    <div class="options-hd">
                                        <span class="sort">
                                            <select
                                                class="browser-default custom-select"
                                                v-model="data.areaId"
                                            >
                                                <option
                                                    v-for="area in areaList"
                                                    :key="area.id"
                                                    :value="area.id"
                                                >{{ area.name[i18n] }}</option>
                                            </select>
                                        </span>
                                        <span class="action">
                                            <div class="btn-group">
                                                <button
                                                    title
                                                    data-toggle="tooltip"
                                                    type="button"
                                                    class="btn btn-xs btn-default waves-effect waves-light"
                                                    :disabled="index == 0"
                                                    @click="$upOption(currentEdit.wish, index)"
                                                >
                                                    <i class="fa fa-chevron-up"></i>
                                                </button>
                                                <button
                                                    title
                                                    data-toggle="tooltip"
                                                    type="button"
                                                    class="btn btn-xs btn-default waves-effect waves-light"
                                                    :disabled="
                                            index == currentEdit.wish.length - 1
                                        "
                                                    @click="$downOption(currentEdit.wish, index)"
                                                >
                                                    <i class="fa fa-chevron-down"></i>
                                                </button>
                                                <button
                                                    title
                                                    data-toggle="tooltip"
                                                    type="button"
                                                    class="btn btn-xs btn-default waves-effect waves-light"
                                                    @click="$removeOption(currentEdit.wish, index)"
                                                >
                                                    <i class="fa fa-times"></i>
                                                </button>
                                            </div>
                                        </span>
                                    </div>
                                    <div class="options-bd">
                                        <div
                                            class="language-item"
                                            v-for="lang in enumData.langs"
                                            :key="'wish_' + index + '_地點_' + lang.key"
                                        >
                                            <label
                                                :for="`地點_${index}_${lang.key}`"
                                            >地點 - {{lang.label}}</label>
                                            <input
                                                :id="`地點_${index}_${lang.key}`"
                                                v-model="data.name[lang.key]"
                                                placeholder="請輸入地點"
                                                class="form-control col-md-12"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <button
                                data-toggle="tooltip"
                                type="button"
                                class="ml-0 btn btn-default btn-sm waves-effect waves-light"
                                @click="addOption"
                            >新增志願</button>
                        </div>
                        <!-- wish modal -->
                        <div class="modal fade" id="wishModal">
                            <div class="modal-dialog modal-dialog-centered">
                                <div class="modal-content">
                                    <!-- Modal Header -->
                                    <div class="modal-header">
                                        <h4 class="modal-title">志願地點</h4>
                                        <button
                                            type="button"
                                            class="close"
                                            data-dismiss="modal"
                                        >&times;</button>
                                    </div>

                                    <!-- Modal body -->
                                    <div class="modal-body">
                                        <div class="form-group">
                                            <label for="wishArea">區域</label>
                                            <select
                                                class="browser-default custom-select"
                                                v-model="wishEdit.areaId"
                                                id="wishArea"
                                            >
                                                <option
                                                    v-for="area in areaList"
                                                    :key="area.id"
                                                    :value="area.id"
                                                >{{ area.name[i18n] }}</option>
                                            </select>
                                        </div>
                                        <div class="form-group">
                                            <div
                                                v-for="lang in enumData.langs"
                                                :key="`地點_${lang.key}`"
                                            >
                                                <label :for="`地點${lang.key}`">地點 - {{lang.label}}</label>
                                                <input
                                                    :id="`地點${lang.key}`"
                                                    v-model="wishEdit.name[lang.key]"
                                                    placeholder="請輸入地點"
                                                    class="form-control col-md-12"
                                                />
                                            </div>
                                        </div>
                                    </div>

                                    <!-- Modal footer -->
                                    <div class="modal-footer">
                                        <button
                                            type="button"
                                            class="btn btn-sm btn-default waves-effect waves-light"
                                            @click="$saveModal('wish')"
                                        >儲存</button>
                                        <button
                                            type="button"
                                            class="btn btn-sm waves-effect waves-light"
                                            @click="$closeModal('wish', wishInit)"
                                        >關閉</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <!-- Modal footer -->
                    <div class="modal-footer">
                        <button
                            type="button"
                            class="btn btn-sm waves-effect waves-light"
                            @click="$closeModal('option')"
                        >關閉</button>
                    </div>
                </div>
            </div>
        </div>
        <!-- wish end -->
    </div>
</template>

<script>
export default {
    name: "wish",
    props: {
        type: {
            type: String,
            required: true,
            default: ""
        },
        i18n: {
            type: String,
            required: true,
            default: ""
        },
        content: {
            type: Object,
            default: function() {
                return {};
            }
        },
        currentEdit: {
            type: Object,
            default: function() {
                return {};
            }
        },
        enumData: {
            type: Object,
            default: function() {
                return {};
            }
        }
    },

    data() {
        return {
            // 已選志願序
            selectedRankList: [],
            // 志願選項
            wishEdit: {
                id: "",
                name: {},
                areaId: ""
            },
            wishInit: {
                name: {},
                areaId: ""
            },
            wishEditIndex: ""
        };
    },
    computed: {
        areaList: function() {
            if (this.enumData.areas) {
                return Object.entries(this.enumData.areas).map(
                    ([key, value]) => {
                        return {
                            id: key,
                            name: value
                        };
                    }
                );
            } else {
                return [];
            }
        }
    },
    created() {},
    methods: {
        addOption() {
            this.currentEdit.wish.push({ areaId: "", name: {} });
        },
        // 取得洲別文字
        getAreaText(id) {
            let area = this.areaList.find(item => {
                return item.id == id;
            });

            return typeof area == "undefined" ||
                typeof area.name[this.i18n] == "undefined"
                ? ""
                : area.name[this.i18n];
        },
        // 判斷志願序是否已選
        rankIsSeleted(num) {
            return this.selectedRankList.some(function(rank) {
                return rank == num;
            });
        },
        openOptionModal() {
            $(`#optionModal`)
                .appendTo("body")
                .modal("show");
        }
    }
};
</script>
