<template>
    <div>
        <label>選項排列方式 <small class="form-text text-muted">(若下方任一選項勾選"額外輸入"自動改為垂直排列)</small></label>
        <div class="form-group">
            <div class="custom-control form-check custom-control-inline">
                <input
                    v-model="currentEdit.setting.inline"
                    :value="false"
                    type="radio"
                    id="display_block"
                    name="display"
                    class="form-check-input"
                    @change="checkInline"
                />
                <label class="form-check-label" for="display_block">垂直排列</label>
            </div>
            <div class="custom-control form-check custom-control-inline">
                <input
                    v-model="currentEdit.setting.inline"
                    :value="true"
                    type="radio"
                    id="display_inline"
                    name="display"
                    class="form-check-input"
                    @change="checkInline"
                />
                <label class="form-check-label" for="display_inline">水平排列</label>
            </div>
            <!-- 若下方任一選項勾選"輸入說明"自動改為垂直排列 -->
        </div>
    </div>
</template>

<script>
export default {
    name: "display",
    props: ["currentEdit"],
    mounted: function() {},
    methods: {
        checkInline() {
            let des = this.currentEdit.options.some(item => {
                if (item.des) {
                    this.currentEdit.setting.inline = false;
                    return true;
                }
            });
            if (!des && this.currentEdit.setting.inline) {
                this.currentEdit.setting.inline = true;
            }
        }
    }
};
</script>
