import Vue from "vue";
import FormEditor from "./components/FormEditor.vue";

Vue.config.productionTip = false;
// list id 顯示值
Vue.prototype.$renderFunction = function(list = [], id, key = "id") {
    setTimeout(() => {
        const found = list.find(element => element[key] == id);
        return found;
    }, 1);

};

// 開啟 modal
Vue.prototype.$editModal = function(name, initObj, index) {
    this[`${name}EditIndex`] = index;
    if (index == "new") {
        this[`${name}Edit`] = JSON.parse(JSON.stringify(initObj))
    } else {
        this[`${name}Edit`] = JSON.parse(JSON.stringify(this.currentEdit[name][index]))
    }
    $(`#${name}Modal`).appendTo("body").modal("show");
};

// 儲存 modal
Vue.prototype.$saveModal = function(name, uuid = false) {
    let target = this[`${name}Edit`]
    if (this[`${name}EditIndex`] == "new") {
        if (uuid) {
            // 新增時新增 uuid
            target = Object.assign(target, {
                uuid: this.$uuid()
            });
        }
        this.currentEdit[name].push(target);
    } else {
        this.currentEdit[name][this[`${name}EditIndex`]] = target;
    }
    let initObj = this[`${name}Init`];
    this.$closeModal(name, initObj);
};

// 關閉 modal
Vue.prototype.$closeModal = function(name, initObj) {
    if (initObj) {
        this[`${name}Edit`] = Object.assign({}, initObj);
    }

    $(`#${name}Modal`).modal("hide");
};

// 移除選項
Vue.prototype.$removeOption = function(options, index) {
    options.splice(index, 1);
};

// 選項上移
Vue.prototype.$upOption = function(options, index) {
    options[index] = options.splice(index - 1, 1, options[index])[0];
};

// 選項下移
Vue.prototype.$downOption = function(options, index) {
    options[index] = options.splice(index + 1, 1, options[index])[0];
};

// 確認訊息
Vue.prototype.$confirm = function(title = "提示", msg = "確認刪除?", thenCB) {
    $(`#confirmModal`).appendTo("body").modal("show");
    let thenCBClose = function() {
        if (thenCB) {
            thenCB()
        }
        $(`#confirmModal`).modal("hide");
    };
    this.confirm = {
        title: title,
        msg: msg,
        thenCB: thenCBClose
    }
}

// 產生 uuid
Vue.prototype.$uuid = function() {
    var d = Date.now();
    if (
        typeof performance !== "undefined" &&
        typeof performance.now === "function"
    ) {
        d += performance.now(); //use high-precision timer if available
    }
    return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
        /[xy]/g,
        function(c) {
            var r = (d + Math.random() * 16) % 16 | 0;
            d = Math.floor(d / 16);
            return (c === "x" ? r : (r & 0x3) | 0x8).toString(16);
        }
    );
}

new Vue({
    render: function(h) {
        // return h(FormEditor);
        const app = h(FormEditor);
        const el = document.querySelector("#form_editor");
        const cbdata = h("span", {
            attrs: {
                id: "cbdata",
                "data-cbdata": el.dataset.cbdata,
            },
        });

        return h("div", [app, cbdata]);
    },
}).$mount("#form_editor");