<template>
    <div class="row">
        <div class="col-6">
            <div
                id="date-picker-example"
                class="mt-0 mb-0 md-form md-outline input-with-post-icon datepicker"
            >
                <input
                    type="text"
                    class="form-control mb-0"
                    :value="date"
                    :placeholder="placeholder"
                    autocomplete="off"
                />

                <i class="fa fa-calendar input-prefix"></i>
            </div>
        </div>
        <div class="col-6">
            <div class="mt-0 mb-0 md-form md-outline input-with-post-icon timepicker">
                <input
                    class="form-control mb-0"
                    type="text"
                    :value="time"
                    :placeholder="placeholder"
                />
                <i class="fa fa-clock-o input-prefix"></i>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "date_timepicker",
    props: ["value", "placeholder"],
    data() {
        return {
            date: "",
            time: ""
        };
    },
    watch: {
        value: function(val) {
            this.date = val.split(" ")[0];
            this.time = val.split(" ")[1];
        }
    },
    mounted: function() {
        const self = this;

        $(this.$el.firstChild).datepicker({
            autoclose: true,
            format: "yyyy-mm-dd",
            onClose: function() {
                self.date = this.get("select", "yyyy-mm-dd");
                self.$emit("input", `${self.date} ${self.time}`);
            }
        });
        setTimeout(() => {
            $(this.$el.lastChild.childNodes[0])
                .timepicker()
                .on("change", function(event) {
                    self.time = event.target.value;
                    self.$emit("input", `${self.date} ${self.time}`);
                });
        }, 1000);
    },
    destroyed: function() {
        $(this.$el.firstChild).datepicker("destroy");
        $(this.$el.lastChild).timepicker("destroy");
    }
};
</script>
